import React, { Fragment , useContext , useRef  } from 'react';
import {Card , Button } from 'react-bootstrap';
import SimpleContext from './../context/SimpleContext';

const ExpensesCard = ({caption ,cost , show_box_edit , handle_edit_box , delet_expenses , handle_edit_bill }) => {
  const context = useContext(SimpleContext);
  const titleref = useRef(null);
  const costref = useRef(null);
  const buttonref = useRef(null);
  
  
  const title_ref = (e) => {
    if (e.key === "Enter")
      costref.current.focus();
  }
  const cost_ref = (e) => {
    if (e.key === "Enter")
      buttonref.current.focus();
  }

  return (
    
//      


  
    < Fragment>
       
      
       <Card className=" col-5 rtl  justify-content-center " style={{marginLeft:"40%" , backgroundColor:"#515D9C",textAlign:"center" ,color:"white"}} >
                     <Card.Header>خرج های این ماه</Card.Header>
        <Card.Body>
                     <Card.Text className="d-inline col-3 " >{ caption}</Card.Text>
          <Card.Text className="col-2 d-inline ml-1">{cost} تومان</Card.Text>
          <br />
          <br/>
          {show_box_edit ?
            <Fragment>
              <input className="form-control bg-white " placeholder="عنوان"
                value={context.input_edit_title} onChange={context.handle_edit_title} ref={titleref} onKeyDown={ title_ref}/>
              <br/>
            <input className="form-control" placeholder="هزینه"
                value={context.input_edit_cost} onChange={context.handle_edit_cost} ref={costref} onKeyDown={ cost_ref}/>
              <br/>
              <Button style={{width:"100%"}} ref={buttonref} onSubmit={handle_edit_bill} onClick={handle_edit_bill}>تایید</Button>
              <br />
              <br/>
           </Fragment>
            :null
          }
         
          <span className="m-3">
            
            <Button className="fa fa-pencil  p-2 bg-warning" style={{ border: "0px", paddingLeft: "3px", width: "30%" }}
              onClick={ handle_edit_box}
            />
          </span>
          
          <span className="m-3">
            <Button className="fa fa-trash  p-2 bg-danger" style={{ border: "0px", paddingLeft: "3px", width: "30%" }}
            onClick={delet_expenses}
            />{" "}

          </span>
        </Card.Body>
      </Card>
      <br/>
          
      </Fragment>
      );
    }
    
    export default ExpensesCard;