import React,{Fragment , useContext , useEffect , useRef} from 'react';
import { Form , FormControl, FormLabel ,Button , FormGroup} from 'react-bootstrap';
import SimpleContext from './../context/SimpleContext';

const Register = () => {
  const context = useContext(SimpleContext);
  const input_ref = useRef(null);
  const button_ref = useRef(null);
  
  useEffect(() => {
    input_ref.current.focus();
})


  return (
    // <SimpleContext.Provider value={{user2:getuser3,user:getuser4}}>
      <Fragment >
        <Form className="col-3 rtl" style={{ backgroundColor: "rgba(159,155,170,255)", marginLeft: "40%", marginTop: "10%" }}
        onSubmit={e=>e.preventDefault()}
        >
        <FormGroup>
          <br/>
          <FormLabel>اسم خود را وارد کنید</FormLabel>
            <FormControl type="text" placeholder="نام خود را وارد کنید" className="p-3 pb-3"
             
            value={context.user2}
            onChange={context.set_user2}
            // value={getuser3}
            // onChange={set_}
              ref={input_ref}
            />
        </FormGroup>
          <Button variant="primary" type="submit" style={{ width: "100%" }}
          
          onSubmit={context.set_user}
          onClick={context.set_user}
          // onSubmit={set_user_3}
          // onClick={set_user_3}
            ref={button_ref}
          >ورود</Button>
          <br/>
        </Form>

{/*         
<form className="justify-content-center"
                        onSubmit={(event) => event.preventDefault()}
                    
                    >
                        <div className="input-group">
                            <input
                                type="text" className="form-control col-3"
                                onChange={context.set_user_name}
                            />
                        </div>
                        <div>
                        
                            <button type="submit" className="btn btn-primary" >اضافه کن</button>
                        </div>
                    </form> */}


      </Fragment>
      // </SimpleContext.Provider>
    
      );
}
 
export default Register;