import React, {Fragment , useState  } from 'react';
import SimpleContext from './../context/SimpleContext';

const GlobalState = (props) => {

    const [getbills, setbills] = useState([]);
    const [gettitle, settitle] = useState("");
    const [getcost, setcost] = useState("");
    const [getuser, setuser] = useState("");
    const [getuser2, setuser2] = useState("");
    const [getedit_title, setedit_title] = useState("");
    const [getedit_cost, setedit_cost] = useState("");
    const [getinput_edit_title, setinput_edit_title] = useState("");
    const [getinput_edit_cost, setinput_edit_cost] = useState("");
    const [setusername, getusername] = useState("");
    

    const set_user_name = (e) => {
        setusername(e.target.value);
    }
    const set_caption = (e) => {
        settitle(e.target.value);

    }
    const set_cost = (e) => {
        // context.cost = parseInt(e.target.value);
        setcost(e.target.value);
    }
    const add_new_expenses = () => {
        const bills = [...getbills];
        const bill = { title: gettitle, cost: getcost ,id:Math.floor(Math.random()*1000) , edit:false};
        bills.push(bill);
        setbills(bills);
        
        settitle("");
        setcost("");
    }

    const set_user = () => {
        setuser(getuser2);
        setuser2("");


    }
    const set_user2 = (e) => {
        setuser2(e.target.value);
    };

    const delete_expenses = (  id) => {
        const bills = [...getbills];
        const filter = bills.filter(bill => bill.id !== id);
        setbills(filter);
    
    }
    const edit_expenses = (event, id) => {
        const bills = [...getbills];
        const index = bills.findIndex(b => b.id === id);
        const bill = bills[index];
        if(getedit_title !== "")
        bill.title = getedit_title;
        if(getedit_cost !== "")
        bill.cost = getedit_cost;
        bills[index] = bill;
        setbills(bills);
        setedit_cost("");
        setedit_title("");

    }
    const handle_show_edit_box = (event , id) => {
       
        const bills = [...getbills];
        const index = bills.findIndex(b => b.id === id);
        const bill = bills[index];
        bill.edit = !bill.edit;
        bills[index] = bill;
            setbills(bills);
        
    }
    const handle_edit_title = (event) => {
        setinput_edit_title(event.target.value);
        
    }
    const handle_edit_cost = (event) => {
        setinput_edit_cost(event.target.value);
        
    }
    const handle_edit_bill = (id) => {
        const bills = [...getbills];
        const index = bills.findIndex(b => b.id === id);
        const bill = bills[index];
        if (getinput_edit_title !== "") {
            bill.title = getinput_edit_title;
        }
        if (getinput_edit_cost !== "") {
            bill.cost = getinput_edit_cost;
        }
        bills[index] = bill;
        setbills(bills);
        setinput_edit_cost("");
        setinput_edit_title("");
        
    }




    return (
        <SimpleContext.Provider value={{
            // user: getusername,
            caption: gettitle,
            cost:getcost,
            set_user_name: set_user_name,
            set_cost:set_cost,
            set_caption: set_caption,
            add_new_expenses: add_new_expenses,
            list_of_expenses: getbills,
            user: getuser,
            user2: getuser2,
            set_user: set_user,
            set_user2: set_user2,
            delete_expenses: delete_expenses,
            edit_title: getedit_title,
            edit_cost: getedit_cost,
            edit_expenses: edit_expenses,
            input_edit_title: getinput_edit_title,
            input_edit_cost: getinput_edit_cost,
            handle_show_edit_box: handle_show_edit_box,
            handle_edit_title: handle_edit_title,
            handle_edit_cost: handle_edit_cost,
            handle_edit_bill:handle_edit_bill,
        }}>
        <Fragment>
            {props.children}
            </Fragment>
            </SimpleContext.Provider>
    );
}
 
export default GlobalState;