import React, { useContext , useEffect}from 'react';
import {  Nav, NavItem } from 'react-bootstrap';
import { withRouter , NavLink} from 'react-router-dom';
import SimpleContext from './../context/SimpleContext';

const NavBar = (props) => {

    const context = useContext(SimpleContext);
    useEffect(() => {
    }, [context.user]);

    return (
        <div >
          
            <Nav variant="pills" className="justify-content-center pb-2" style={{backgroundColor:"rgba(159,155,170,255)" }}>
                <span className="mr-4">
                    {context.user !== "" ? <p  className="btn btn-success">  سلام  {context.user}</p> : null}
                   
                </span>
               
                <NavItem>
                    <NavLink to="/about" activeStyle={{backgroundColor:"rgba(159,155,170,255)"}}  className="btn" exact><h5>درباره سازنده</h5></NavLink>
                </NavItem>
                <NavItem>
                    <NavLink to="/register"  activeStyle={{backgroundColor:"rgba(159,155,170,255)"}} className="btn"
                        exact><h5>ورود</h5></NavLink>
                </NavItem>
            <NavItem>
                    <NavLink to="/expenses" activeStyle={{backgroundColor:"rgba(159,155,170,255)"}} className="btn" exact><h5>مخارج</h5></NavLink>
                </NavItem>
                <NavItem>
                    <NavLink to="/" activeClassName="active" activeStyle={{backgroundColor:"rgba(159,155,170,255)"}} className="btn"
                        exact><h5>خانه</h5></NavLink>
                </NavItem>
            </Nav>
      </div>
    );
}
 
export default withRouter(NavBar);