import React, { Fragment ,  useContext , useRef , useEffect } from 'react';

import{InputGroup , FormControl ,  Form , Button}from 'react-bootstrap'

import SimpleContext from '../context/SimpleContext';
import PrintExpenses from './PrintExpenses';

const ExpensesNav = () => {
    const context = useContext(SimpleContext);
    const inputref1 = useRef(null);
    const inputref2 = useRef(null);
    const buttonref = useRef(null);
    
    useEffect(() => {
        inputref1.current.focus();
})

    const inputref1_func = (e) => {
        if (e.key === "Enter") {
            inputref2.current.focus();
    }
    }
    const inputref2_func = (e) => {
        if (e.key === "Enter") {
            buttonref.current.focus();
        }
    }
    const inputref3_func = (e) => {
        if (e.key === "Enter") {
            inputref1.current.focus();
        }
    }
    

  

    return (
        <Fragment >
            <Form onSubmit={e => e.preventDefault()}>
           <InputGroup className="rtl mt-5 justify-content-center"  size="lg">
                    <FormControl placeholder="بابت؟" className="col-3  " onChange={context.set_caption} value={context.caption} ref={inputref1} onKeyDown={ inputref1_func} required/>
                    <FormControl placeholder="چقدر" className="col-3 " onChange={context.set_cost} value={ context.cost} ref={inputref2} onKeyDown={ inputref2_func} required/>
                <InputGroup.Append>
                        <InputGroup.Text className="bg-warning">تومان</InputGroup.Text>
                        <Button className="bg-primary" type="submit"
                             onClick={context.add_new_expenses } ref={buttonref} onKeyDown={inputref3_func}>اضافه کن</Button>
                </InputGroup.Append>
            </InputGroup>
               </Form>
          <PrintExpenses/>
 
         </Fragment>

     );
}

export default ExpensesNav;



