import  {  useState , useRef , useEffect } from 'react';
import { Button, Table } from 'react-bootstrap';



const Tablecal = () => {
   
    ///usestate
    const [getinput, setinput] = useState('');

    ///useref
    const inputref = useRef('');
    
    ///ueeffect
     useEffect(() => {
        inputref.current.focus();
      
    });
  
   
    const handleclick = (e) => {
        setinput(getinput.concat(e.target.value));
    }
    const clear = () => {
        setinput("");
    }
    const backspace = () => {
        setinput(getinput.slice(0, -1));
    }
    const result = () => {
        try {
            setinput(eval(getinput).toString());
        }
        catch {
            setinput("error");
        }
    }
    

    

    return (
       
   <div className="bg-info position-relative">
   
              <Table style={{
                 width: "320px",
                top: "50%",
                left: "45%",
                position: "absolute",
            }} className="flex " id="table">

                <tbody className="flex ">
                    <tr> <td colSpan="4">
                        <input  ref={inputref}
                            value={getinput}
                            className="form-inline form-control"
                            style={{ backgroundColor: "#1d2951",color:"white" }}
                            type="text"
                            // onChange={()=>context.check_data_ref(inputref.current.value)}
                            onChange={(e)=>setinput(e.target.value)}
                            
                        />
                    </td>
                    </tr>
                   
            <tr className="align-content-center ">
                      <td><Button id="btn" className=" btn btn-block  " value="1" onClick={(event) => handleclick(event)}>1</Button></td>
                      <td><Button id="btn" className=" btn btn-block  " value="2" onClick={(event) => handleclick(event)}>2</Button></td>
                      <td ><Button id="btn" className=" btn btn-block "  value="3" onClick={(event) => handleclick(event)}>3</Button></td>
                      <td ><Button id="btn" className=" btn btn-block bg-warning "  value="-" onClick={(event) => handleclick(event)}>&ndash;</Button></td>
                    </tr>
                   
                    <tr>
                    <td><Button id="btn" className=" btn btn-block " value="4" onClick={(event) => handleclick(event)} >4</Button></td>
                    <td> <Button id="btn" className=" btn btn-block " value="5" onClick={(event) => handleclick(event)} >5</Button></td>
                    <td> <Button id="btn" className=" btn btn-block " value="6" onClick={(event) => handleclick(event)}>6</Button></td>
                    <td> <Button id="btn" className=" btn btn-block bg-warning " value="+" onClick={(event) => handleclick(event)} >+</Button></td>
                    </tr>
                    <tr>
                    <td><Button id="btn" className=" btn btn-block " value="7" onClick={(event) => handleclick(event)}>7</Button></td>
                    <td><Button id="btn" className=" btn btn-block " value="8" onClick={(event) => handleclick(event)} >8</Button></td>
                    <td><Button id="btn" className=" btn btn-block " value="9" onClick={(event) => handleclick(event)} >9</Button></td>
                        <td > <Button id="btn" className=" btn btn-block bg-warning " value="/" onClick={(event) => handleclick(event)} >/</Button> </td>
                    </tr>
                   <tr>
                    <td> <Button id="btn" className=" btn btn-block bg-warning" value="*" onClick={(event) => handleclick(event)}>*</Button> </td>
                    <td> <Button id="btn" className=" btn btn-block "value="0" onClick={(event) => handleclick(event)} >0</Button> </td>
                            <td > <Button id="btn" className=" btn btn-block bg-warning" value="." onClick={(event) => handleclick(event)} >.</Button> </td>
                        <td rowSpan="2"> <Button id="btn" className=" btn btn-block bg-warning " onClick={result}
                            style={{ height:"110px" }} >=</Button> </td>
                    </tr>
                   
                    <tr >
                        <td colSpan="2">
                            <Button id="btn" className="btn btn-block bg-warning" 
                            onClick={clear}
                            >clear</Button>
                        </td>
                    <td> <Button id="btn" className=" btn btn-block bg-warning " onClick={backspace}>c</Button> </td>

                    </tr>
</tbody>
</Table>
        </div>
     );
}
 
export default Tablecal;