import React,{Fragment,useContext} from 'react';
import SimpleContext from './../context/SimpleContext';
import ExpensesCard from './ExpensesCard';

const PrintExpenses = () => {
    const context = useContext(SimpleContext);
    return (
        <Fragment>
            {context.list_of_expenses.map((ex) => (
                <ExpensesCard
                    caption={ex.title}
                    cost={ex.cost}
                    show_box_edit={ex.edit}
                    key={ex.id}
                    handle_edit_box={(event) => context.handle_show_edit_box( event, ex.id)}
                    delet_expenses={()=>context.delete_expenses(ex.id)}
                    handle_edit_bill={()=>context.handle_edit_bill( ex.id)}
                />
            ))}
        </Fragment>
    );
}
 
export default PrintExpenses;